.footer {
  position: relative;
  width: 100%;
  background-color: #333;
  padding: 30px 0; }
  .footer h3 {
    margin-bottom: .4em;
    line-height: 1.5;
    font-size: 20px;
    font-weight: 400;
    color: #888; }
  .footer .footer-left {
    width: 50%;
    float: left;
    padding: 0 10px;
    color: #888; }
    .footer .footer-left p {
      font-size: 14px; }
  .footer .footer-mid {
    width: 25%;
    float: left;
    padding: 0 10px; }
    .footer .footer-mid ul li {
      margin-top: 5px; }
      .footer .footer-mid ul li a {
        color: #888;
        text-decoration: none;
        font-size: 14px; }
        .footer .footer-mid ul li a:hover {
          color: #fff;
          text-decoration: underline;
          -webkit-transition: all 0.5s;
          -o-transition: all 0.5s;
          transition: all 0.5s; }
  .footer .footer-right {
    float: left;
    padding: 0 10px; }
    .footer .footer-right a {
      color: #888;
      font-size: 30px; }
      .footer .footer-right a:hover {
        color: #fff;
        -webkit-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s; }
  .footer .design {
    border-top: 1px solid #444;
    padding: 20px 15px 0;
    color: #888;
    text-align: center;
    font-size: 14px; }



