.modal-container {
  background-color: rgba(33, 33, 33, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 1;
  z-index: 99; }
  .modal-container .modal-body {
    background-color: #fff;
    border-radius: 5px;
    padding: 20px 30px;
    width: 400px;
    position: absolute;
    left: 50%;
    top: 40%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-animation: fadeIn 0.5s ease-in-out;
    animation: fadeIn 0.5s ease-in-out; }
    .modal-container .modal-body .modal-close {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: 40px;
      height: 36px;
      text-align: center;
      color: #ddd;
      font: bold 20px/36px "simsun";
      -webkit-transition: 0.1s;
      -o-transition: 0.1s;
      transition: 0.1s;
      cursor: pointer; }
    .modal-container .modal-body .modal-title {
      text-align: center;
      font-size: 18px;
      font-weight: bold; }
    .modal-container .modal-body .modal-content {
      padding: 24px 0px; }
    .modal-container .modal-body .modal-footer {
      text-align: center; }
      .modal-container .modal-body .modal-footer .ok-btn {
        background-color: #1890ff;
        color: #fff; }
        .modal-container .modal-body .modal-footer .ok-btn:hover {
          cursor: pointer;
          background-color: #40a9ff; }
      .modal-container .modal-body .modal-footer .cancel-btn {
        border: 1px solid #d9d9d9;
        background-color: #fff; }
        .modal-container .modal-body .modal-footer .cancel-btn:hover {
          cursor: pointer;
          color: #40a9ff;
          border-color: #40a9ff; }
      .modal-container .modal-body .modal-footer button {
        margin: 0 20px;
        padding: 6px 20px;
        font-size: 16px;
        border-radius: 4px;
        border: 0;
        outline: none; }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translate(-50%, -55%);
    transform: translate(-50%, -55%); }
  100% {
    opacity: 1;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%); } }

@keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translate(-50%, -55%);
    transform: translate(-50%, -55%); }
  100% {
    opacity: 1;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%); } }



